@import "functions.scss";
@import "varibles.scss";
.net-content-wraper {
  .net-wraper {
    padding: 20px 15px;
    background-color: $white;
    border-radius: 5px;
    margin-bottom: 20px;
    .net-title {
      font-family: $Ghotam;
      font-size: size(16);
      font-weight: bold;
      text-transform: uppercase;
      color: $black600;
      margin-bottom: 20px;
    }
    .net-desc {
      font-family: $Ghotam;
      font-size: size(14);
      color: $porple200;
    }
    .net-item-container {
      display: flex;
      justify-content: flex-start;
      flex-wrap: wrap;
      .net-item {
        border-radius: 5px;
        background-color: $porple150;
        max-width: size(250);
        margin: 10px 5px 20px 5px;
        overflow: hidden;
        &:hover {
          .net-user-bg {
            &::after {
              opacity: 0.1;
            }
          }
          .user-avatar {
            transform: scale(1.05) translate(-50%, -50%);
          }
        }
        .net-user-bg {
          position: relative;
          overflow: hidden;
          &::after {
            position: absolute;
            content: "";
            top: 0;
            left: 0;
            height: 100%;
            width: 100%;
            background-color: $white;
            opacity: 0.5;
            z-index: 0;
            transition: 0.4s;
          }
        }
        .user-avatar {
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          height: 100px;
          width: 100px;
          border-radius: 50%;
          z-index: 2;
          transition: 0.4s;
        }
        .user-bg {
          height: 150px;
          width: 100%;
          object-fit: cover;
        }
        .net-text-wraper {
          padding: 15px;
          .user-name {
            font-family: $Ghotam;
            font-size: size(16);
            font-weight: 600;
            color: $black600;
            margin-bottom: 15px;
          }
        }
      }
    }
  }
}
