@import "functions.scss";
@import "varibles.scss";
.sidebar-wraper {
  max-width: size(290);
  .sidebar-bg {
    width: 100%;
    background-color: $white200;
    padding: size(25) size(30);
    margin-bottom: 20px;
  }
  .user-wraper {
    margin-bottom: 20px;
    width: 100%;
    .user-bg {
      img {
        height: 100%;
        width: 100%;
      }
    }
    .user-data {
      position: relative;
      padding: 66px 26px 26px;
      background-color: $white;
      img {
        position: absolute;
        top: 0%;
        left: 50%;
        transform: translate(-50%, -50%);
      }
      .main-user-name {
        font-weight: bold;
        margin-bottom: 10px;
        text-align: center;
      }
      .user-desc {
        font-family: $Ghotam;
        font-size: size(12);
        font-weight: 400;
        max-width: size(240);
        text-align: center;
        line-height: 120%;
      }
    }
  }
  .new-article {
    width: 100%;
    background-color: $white;
    padding: 20px 10px;
    margin-bottom: 20px;
    .article-btn {
      font-family: $Ghotam;
      font-size: 12px;
      line-height: 11px;
      text-align: center;
      text-transform: uppercase;
      color: $white;
      width: 100%;
      padding: 11px;
      text-align: center;
      background: $btnGradient;
      border-radius: 4px;
      border: none;
      cursor: pointer;
      transition: 0.4s;
      &:hover {
        transform: scale(1.02);
        background-color: $btnGradientHov;
      }
    }
  }
  .groups {
    .flex-container {
      display: flex;
      justify-content: space-between;
      width: 100%;
      align-items: center;
      border-bottom: 1px solid $porple500;
      padding-bottom: 20px;
      margin-bottom: 20px;
      .sidebar-edit {
        font-family: $Ghotam;
        font-size: size(12);
        line-height: 11px;
        text-align: right;
        text-transform: uppercase;
        color: $blue100;
      }
    }
    .flex-group-item a {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      margin-bottom: 10px;
      background-color: $white;
      border-radius: 6px;
      border: 1px solid $porple500;
      padding: 15px;
      img {
        height: 52px;
        width: 52px;
        margin-right: 15px;
      }
      .group-name-desc {
        font-family: $Ghotam;
        font-size: size(14);
        font-weight: bold;
        line-height: 125%;
        max-width: size(150);
      }
    }
    .all-group {
      font-family: $Ghotam;
      font-size: size(12);
      line-height: 11px;
      text-transform: uppercase;
      color: $blue100;
    }
  }
  .hashTags {
    .sidebar-title {
      padding-bottom: 20px;
      border-bottom: 1px solid $porple500;
      margin-bottom: 20px;
    }
    .flex-container-w {
      display: flex;
      flex-wrap: wrap;
      .hashtag-item {
        margin: 10px;
        padding: 7px;
        border-radius: 4px;
        background-color: $porple150;
        text-align: center;
        cursor: pointer;
        transition: 0.4s;
        &:hover {
          transform: scale(1.01);
        }
      }
    }
  }
  .articles {
    .sidebar-title {
      padding-bottom: 20px;
      border-bottom: 1px solid $porple500;
      margin-bottom: 20px;
    }
    .article-list {
      .article-item a {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 15px;
        img {
          margin-right: 15px;
        }
        .art-title {
          font-family: $Ghotam;
          font-size: size(14);
          font-weight: 600;
          line-height: 125%;
          color: $black600;
          margin-bottom: 5px;
        }
        .viewers {
          font-family: $Ghotam;
          font-size: 10px;
          line-height: 150%;
          color: $black600;
          font-weight: bold;
        }
      }
    }
  }
}
