@import "functions.scss";
@import "varibles.scss";
.chat-wraper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  .chat-list {
    max-width: size(450);
    padding: 0;
    .chat-item {
      display: flex;
      justify-content: space-between;
      align-items: center;
      background-color: $white;
      padding: 15px 10px;
      border-bottom: 1px solid $porple100;
      cursor: pointer;
      transition: 0.4s;
      &:hover {
        transform: scale(1.01);
        background-color: $porple100;
      }
      img {
        height: 50px;
        width: 50px;
        border-radius: 50%;
        border: 3px solid $porple100;
      }
      .text-wraper {
        padding-left: 15px;
        .writer {
          font-family: $Ghotam;
          font-size: size(16);
          font-weight: 500;
          color: $black600;
          margin-bottom: 5px;
        }
        .writen-time {
          font-family: $Ghotam;
          font-size: size(14);
          font-weight: 400;
          color: $porple200;
          margin-bottom: 5px;
        }
      }
    }
    .active {
      transform: scale(1.01);
      background-color: $porple100;
    }
  }
}
