.sidebar-wraper {
  max-width: 18.125rem;
}

.sidebar-wraper .sidebar-bg {
  width: 100%;
  background-color: #fcfdfd;
  padding: 1.5625rem 1.875rem;
  margin-bottom: 20px;
}

.sidebar-wraper .user-wraper {
  margin-bottom: 20px;
  width: 100%;
}

.sidebar-wraper .user-wraper .user-bg img {
  height: 100%;
  width: 100%;
}

.sidebar-wraper .user-wraper .user-data {
  position: relative;
  padding: 66px 26px 26px;
  background-color: #fff;
}

.sidebar-wraper .user-wraper .user-data img {
  position: absolute;
  top: 0%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.sidebar-wraper .user-wraper .user-data .main-user-name {
  font-weight: bold;
  margin-bottom: 10px;
  text-align: center;
}

.sidebar-wraper .user-wraper .user-data .user-desc {
  font-family: "Ghotem", "Roboto", sans-serif;
