}

.sidebar-wraper .new-article {
  width: 100%;
  background-color: #fff;
  padding: 20px 10px;
  margin-bottom: 20px;
}

.sidebar-wraper .new-article .article-btn {
  font-family: "Ghotem", "Roboto", sans-serif;
  font-size: 12px;
  line-height: 11px;
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  width: 100%;
  padding: 11px;
  text-align: center;
  background: -webkit-gradient(linear, left top, left bottom, from(#0077b5), to(#0e6795));
  background: linear-gradient(180deg, #0077b5 0%, #0e6795 100%);
  border-radius: 4px;
  border: none;
  cursor: pointer;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.sidebar-wraper .new-article .article-btn:hover {
  -webkit-transform: scale(1.02);
          transform: scale(1.02);
  background-color: linear-gradient(180deg, #0077b5 0%, #185370 100%);
}

