.post-container {
  max-width: 53.125rem;
  margin-right: 2.5rem;
}

.post-container .post-wraper {
  background-color: #fff;
  margin-bottom: 20px;
}

.post-container .post-wraper .post-header {
  padding: 15px;
  border-bottom: 1px solid #f4f4f4;
}

.post-container .post-wraper .post-header .desc {
  font-family: "Ghotem", "Roboto", sans-serif;
  font-size: 0.75rem;
  font-weight: 500;
  color: #181818;
}

.post-container .post-wraper .post-header .desc a {
  color: #0275b1;
}

.post-container .post-wraper .post-body {
  padding: 14px 30px;
}

.post-container .post-wraper .post-body .post-body-header {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  margin-bottom: 15px;
}

.post-container .post-wraper .post-body .post-body-header .text-wraper {
  padding-left: 15px;
}

.post-container .post-wraper .post-body .post-body-header .text-wraper .post-user-name {
  margin-bottom: 5px;
}

.post-container .post-wraper .post-body .post-body-header .text-wraper .post-user-name a {
  font-family: "Ghotem", "Roboto", sans-serif;
  font-size: 0.875rem;
  font-weight: bold;
  color: #181818;
}

.post-container .post-wraper .post-body .post-body-header .text-wraper .user-job {
  font-family: "Ghotem", "Roboto", sans-serif;
  font-size: 0.625rem;
  color: #181818;
  font-weight: 500;
}

.post-container .post-wraper .post-body .post-desc {
  position: relative;
  font-family: "Ghotem", "Roboto", sans-serif;
  font-size: 0.875rem;
  line-height: 1.3125rem;
  color: #181818;
  max-height: 60px;
  overflow-y: hidden;
  margin-bottom: 15px;
}

.post-container .post-wraper .post-body .post-desc::after {
  position: absolute;
  bottom: 0;
  left: 0;
  content: "";
  width: 100%;
  height: 100%;
  background: -webkit-gradient(linear, left top, left bottom, from(transparent), to(#fff));
  background: linear-gradient(180deg, transparent, #fff);
}

.post-container .post-wraper .post-body .visible-text {
  height: 100%;
  overflow-y: visible;
}

.post-container .post-wraper .post-body .visible-text::after {
  display: none;
}

.post-container .post-wraper .post-body img {
  margin-bottom: 15px;
}

.post-container .post-wraper .post-body .download-item {
  max-width: 100%;
  padding: 20px;
  background-color: #e9f0f8;
  margin-bottom: 10px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}

.post-container .post-wraper .post-body .download-item .text-wraper {
  padding-left: 15px;
}

.post-container .post-wraper .post-body .download-item .text-wraper .file-name {
  font-family: "Ghotem", "Roboto", sans-serif;
  font-size: 0.875rem;
  line-height: 1.3125rem;
  font-weight: bold;
  color: #181818;
  margin-bottom: 5px;
}

.post-container .post-wraper .post-body .download-item .text-wraper .file-desc {
  font-family: "Ghotem", "Roboto", sans-serif;
  font-size: 0.625rem;
  line-height: szie(15);
  color: #181818;
}

.post-container .post-wraper .post-body .download-item .file-link {
  display: block;
  margin-left: auto;
}

.post-container .post-wraper .post-footer {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  border-top: 1px solid #f4f4f4;
  padding: 0;
  height: 50px;
}

.post-container .post-wraper .post-footer button {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 15px;
  border: none;
  background: transparent;
  cursor: pointer;
}

.post-container .post-wraper .post-footer .liked,
.post-container .post-wraper .post-footer .coment {
  height: 100%;
  border-right: 1px solid #f4f4f4;
}

.post-container .post-wraper .post-footer .liked span,
.post-container .post-wraper .post-footer .coment span {
  margin-left: 5px;
  display: block;
  font-family: "Ghotem", "Roboto", sans-serif;
  font-size: 1rem;
  line-height: 0.8125rem;
  font-weight: bold;
}

.post-container .post-wraper .post-footer .share {
  margin-left: auto;
}

.post-container .post-wraper .post-footer .share span {
  margin-left: 5px;
  display: block;
  font-family: "Ghotem", "Roboto", sans-serif;
  font-size: 1rem;
  line-height: 0.8125rem;
  font-weight: bold;
}
