@import "functions.scss";
@import "varibles.scss";
.post-container {
  max-width: size(850);
  margin-right: size(40);
  .post-wraper {
    background-color: $white;
    margin-bottom: 20px;
    .post-header {
      padding: 15px;
      border-bottom: 1px solid $porple500;
      .desc {
        font-family: $Ghotam;
        font-size: size(12);
        font-weight: 500;
        color: $black600;
        a {
          color: $blue100;
        }
      }
    }
    .post-body {
      padding: 14px 30px;
      .post-body-header {
        display: flex;
        align-items: center;
        margin-bottom: 15px;
        .text-wraper {
          padding-left: 15px;
          .post-user-name {
            margin-bottom: 5px;
            a {
              font-family: $Ghotam;
              font-size: size(14);
              font-weight: bold;
              color: $black600;
            }
          }
          .user-job {
            font-family: $Ghotam;
            font-size: size(10);
            color: $black600;
            font-weight: 500;
          }
        }
      }
      .post-desc {
        position: relative;
        font-family: $Ghotam;
        font-size: size(14);
        line-height: size(21);
        color: $black600;
        max-height: 60px;
        overflow-y: hidden;
        margin-bottom: 15px;
        &::after {
          position: absolute;
          bottom: 0;
          left: 0;
          content: "";
          width: 100%;
          height: 100%;
          background: linear-gradient(180deg, transparent, #fff);
        }
      }
      .visible-text {
        height: 100%;
        overflow-y: visible;
        &::after {
          display: none;
        }
      }
      img {
        margin-bottom: 15px;
      }
      .download-item {
        max-width: 100%;
        padding: 20px;
        background-color: $porple150;
        margin-bottom: 10px;
        display: flex;
        .text-wraper {
          padding-left: 15px;
          .file-name {
            font-family: $Ghotam;
            font-size: size(14);
            line-height: size(21);
            font-weight: bold;
            color: $black600;
            margin-bottom: 5px;
          }
          .file-desc {
            font-family: $Ghotam;
            font-size: size(10);
            line-height: szie(15);
            color: $black600;
          }
        }
        .file-link {
          display: block;
          margin-left: auto;
        }
      }
    }
    .post-footer {
      display: flex;
      align-items: center;
      border-top: 1px solid $porple500;
      padding: 0;
      height: 50px;
      button {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 15px;
        border: none;
        background: transparent;
        cursor: pointer;
      }
      .liked,
      .coment {
        height: 100%;
        border-right: 1px solid $porple500;
        span {
          margin-left: 5px;
          display: block;
          font-family: $Ghotam;
          font-size: size(16);
          line-height: size(13);
          font-weight: bold;
        }
      }
      .share {
        margin-left: auto;
        span {
          margin-left: 5px;
          display: block;
          font-family: $Ghotam;
          font-size: size(16);
          line-height: size(13);
          font-weight: bold;
        }
      }
    }
  }
}
