.chat-wraper {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}

.chat-wraper .chat-list {
  max-width: 28.125rem;
  padding: 0;
}

.chat-wraper .chat-list .chat-item {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  background-color: #fff;
  padding: 15px 10px;
  border-bottom: 1px solid #cecece;
  cursor: pointer;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.chat-wraper .chat-list .chat-item:hover {
  -webkit-transform: scale(1.01);
          transform: scale(1.01);
  background-color: #cecece;
}

.chat-wraper .chat-list .chat-item img {
  height: 50px;
  width: 50px;
  border-radius: 50%;
  border: 3px solid #cecece;
}

.chat-wraper .chat-list .chat-item .text-wraper {
  padding-left: 15px;
}

.chat-wraper .chat-list .chat-item .text-wraper .writer {
  font-family: "Ghotem", "Roboto", sans-serif;
  font-size: 1rem;
  font-weight: 500;
  color: #181818;
  margin-bottom: 5px;
}

.chat-wraper .chat-list .chat-item .text-wraper .writen-time {
  font-family: "Ghotem", "Roboto", sans-serif;
  font-size: 0.875rem;
  font-weight: 400;
  color: #747474;
  margin-bottom: 5px;
}

.chat-wraper .chat-list .active {
  -webkit-transform: scale(1.01);
          transform: scale(1.01);
  background-color: #cecece;
}
