@import "varibles.scss";
@import "functions.scss";
footer {
  background-color: $porple300;
  border-top: 1px solid $porple400;
  padding: 35px 0 55px;
  .container {
    .flex-container {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      .footer-title {
        margin-bottom: 30px;
      }
      .footer-item-wraper {
        &:nth-child(3),
        &:nth-child(4) {
          margin-top: 30px;
        }
        .footer-list {
          .footer-item {
            margin-bottom: 15px;
            a {
              font-family: $Ghotam;
              font-size: size(12);
              font-weight: 400;
              color: $black600;
              transition: 0.4s;
              &:hover {
                color: $blue100;
              }
            }
          }
        }
        .btn {
          display: block;
          font-family: $Ghotam;
          font-size: size(12);
          text-transform: uppercase;
          width: 180px;
          border-radius: 4px;
          border: 1px solid $blue100;
          padding: 11px;
          text-align: center;
          margin-bottom: 10px;
          color: $blue100;
          transition: 0.4s;
          svg {
            vertical-align: middle;
            margin-left: 30%;
            stroke: $blue100;
          }
          &:hover {
            background-color: $blue100;
            color: $white;
            svg {
              stroke: $white;
            }
          }
        }
        .active {
          background-color: $blue100;
          color: $white;
          svg path {
            stroke: $white;
          }
          &:hover {
            color: $blue100;
            background-color: transparent;
            svg path {
              stroke: $blue100;
            }
          }
        }
        .select-lang {
          width: size(270);
          li {
            width: 100%;
            font-family: $Ghotam;
            font-size: size(12);
            font-weight: 600;
            text-transform: uppercase;
            text-align: center;
            color: $black600;
            background-color: $white;
            padding: 10px;
            cursor: pointer;
          }
        }
      }
    }
  }
}
@media screen and(max-width:800px) {
  footer {
    padding-bottom: 80px;
    .container {
      .flex-container {
        justify-content: space-between;
        .footer-item-wraper {
          width: 50%;
        }
      }
    }
  }
}
@media screen and(max-width:490px) {
  footer {
    .container {
      .flex-container {
        justify-content: center;
        .footer-title {
          text-align: center;
        }
        .footer-item-wraper {
          width: 100%;
          .footer-list {
            li {
              text-align: center;
            }
          }
          .btn {
            width: 90%;
            margin: 20px auto;
          }
          .select-lang {
            width: 100%;
            li {
              width: 90%;
              margin: 0 auto;
            }
          }
        }
      }
    }
  }
}
