@import "functions.scss";
@import "varibles.scss";

header {
  background-color: #fff;
  .header-container {
    max-width: size(1440);
    margin: 0 auto;
    padding: 0px 10px;
    .flex-container {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      align-items: center;
      min-width: 100%;
      .logo {
        margin-right: 44px;
      }
      nav {
        width: size(450);
        border-left: 1px solid #f4f4f4;
        border-right: 1px solid #f4f4f4;
        .header-menu {
          display: flex;
          justify-content: space-between;
          min-width: 100%;
          .menu-item {
            border-bottom: 2px solid transparent;
            transition: 0.4s;
            a {
              display: inline-flex;
              flex-direction: column;
              justify-content: center;
              align-items: center;
              padding: size(17) size(10);
            }
            svg {
              display: block;
              width: 24px;
              margin-bottom: 10px;
              transition: 0.4s;
              stroke: $black600;
            }
            .item-desc {
              font-family: $Ghotam;
              font-size: size(12);
              font-weight: 600;
              text-transform: uppercase;
              color: $black600;
              transition: 0.4s;
            }
            &:hover {
              .item-desc {
                color: $blue100;
              }
              svg path {
                stroke: $blue100;
              }
            }
            .acticve {
              border-bottom: 2px solid $blue100;
            }
            &:hover {
              border-bottom: 2px solid $blue100;
            }
          }
        }
      }
      .head-search {
        position: relative;
        padding: size(17) size(10);
        width: size(367);
        border-right: 1px solid #f4f4f4;
        input {
          border: none;
          outline: none;
          background: none;
        }
        .srch-inp {
          font-family: $Ghotam;
          font-size: size(16);
          line-height: size(15);
          color: $porple100;
          width: 80%;
          padding: size(17) size(10) size(17) size(50);
          transition: 0.4s;
          &:focus {
            border: 1px solid #f4f4f4;
          }
          &::placeholder {
            font-family: $Ghotam;
            font-size: size(16);
            line-height: size(15);
            color: $porple100;
            padding-left: size(40);
          }
        }
        .send-btn {
          position: absolute;
          top: 50%;
          left: 30px;
          height: 24px;
          width: 24px;
          transform: translateY(-50%);
          background-image: url(../../public/img/search.svg);
          background-size: cover;
          background-position: center center;
        }
      }
      .account-data {
        padding: size(17) size(30);
        display: inline-flex;
        justify-content: space-between;
        img {
          margin-right: size(15);
          height: 42px;
          width: 42px;
          border-radius: 50%;
        }
        .user-text-wraper {
          .user-name {
            span {
              margin-left: 10px;
              font-family: $Ghotam;
              font-size: size(12);
              line-height: size(11);
              text-transform: uppercase;
              color: rgba(24, 24, 24, 0.2);
            }
          }
          .view-text {
            font-family: $Ghotam;
            font-size: size(12);
            line-height: size(11);
            color: $porple200;
            margin-top: 11px;
            .statistic-desc {
              position: relative;
              font-weight: 600;
              margin-left: 5px;
              color: $green100;
              &:after {
                position: absolute;
                content: "";
                top: 50%;
                right: -75%;
                height: 12px;
                width: 12px;
                transform: translateY(-50%);
                background-image: url(../../public/img/arrow-up-right.svg);
                background-position: center center;
                background-size: cover;
              }
            }
          }
        }
      }
      .other-wraper {
        padding: size(30) size(23);
        border-left: 1px solid #f4f4f4;
        border-right: 1px solid #f4f4f4;
        .other-btn {
          position: relative;
          font-family: $Ghotam;
          font-size: size(12);
          font-weight: bold;
          line-height: size(11);
          text-align: center;
          text-transform: uppercase;
          color: $black600;
          border: none;
          background-color: transparent;
          &:after {
            position: absolute;
            content: "";
            top: -200%;
            left: 50%;
            transform: translateX(-50%);
            height: 24px;
            width: 24px;
            background-image: url(../../public/img/more-horizontal.svg);
            background-size: contain;
            background-position: center center;
          }
        }
      }
    }
  }
}
@media screen and(max-width:900px) {
  body {
    position: relative;
  }
  header {
    .header-container {
      padding: 0;
      .flex-container {
        justify-content: flex-end;
        .logo {
          display: none;
        }
        nav {
          position: fixed;
          left: 0;
          bottom: 0;
          width: 100%;
          background-color: #fff;
        }
        .head-search {
          width: 70%;
          .send-btn {
            left: 7px;
          }
        }
        .account-data {
          padding: 0;
          position: absolute;
          top: 10px;
          left: 10px;
          .user-text-wraper {
            display: none;
          }
        }
      }
    }
  }
}
