header {
  background-color: #fff;
}

header .header-container {
  max-width: 90rem;
  margin: 0 auto;
  padding: 0px 10px;
}

header .header-container .flex-container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  min-width: 100%;
}

header .header-container .flex-container .logo {
  margin-right: 44px;
}

header .header-container .flex-container nav {
  width: 28.125rem;
  border-left: 1px solid #f4f4f4;
  border-right: 1px solid #f4f4f4;
}

header .header-container .flex-container nav .header-menu {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  min-width: 100%;
}

header .header-container .flex-container nav .header-menu .menu-item {
  border-bottom: 2px solid transparent;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

header .header-container .flex-container nav .header-menu .menu-item a {
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 1.0625rem 0.625rem;
}

header .header-container .flex-container nav .header-menu .menu-item svg {
  display: block;
  width: 24px;
  margin-bottom: 10px;
  -webkit-transition: 0.4s;
  transition: 0.4s;
  stroke: #181818;
}

header .header-container .flex-container nav .header-menu .menu-item .item-desc {
  font-family: "Ghotem", "Roboto", sans-serif;
  font-size: 0.75rem;
  font-weight: 600;
  text-transform: uppercase;
  color: #181818;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

header .header-container .flex-container nav .header-menu .menu-item:hover .item-desc {
  color: #0275b1;
}

header .header-container .flex-container nav .header-menu .menu-item:hover svg path {
  stroke: #0275b1;
}

header .header-container .flex-container nav .header-menu .menu-item .acticve {
  border-bottom: 2px solid #0275b1;
}

header .header-container .flex-container nav .header-menu .menu-item:hover {
  border-bottom: 2px solid #0275b1;
}

header .header-container .flex-container .head-search {
  position: relative;
  padding: 1.0625rem 0.625rem;
  width: 22.9375rem;
  border-right: 1px solid #f4f4f4;
}

header .header-container .flex-container .head-search input {
  border: none;
  outline: none;
  background: none;
}

header .header-container .flex-container .head-search .srch-inp {
  font-family: "Ghotem", "Roboto", sans-serif;
  font-size: 1rem;
  line-height: 0.9375rem;
  color: #cecece;
  width: 80%;
  padding: 1.0625rem 0.625rem 1.0625rem 3.125rem;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

header .header-container .flex-container .head-search .srch-inp:focus {
  border: 1px solid #f4f4f4;
}

header .header-container .flex-container .head-search .srch-inp::-webkit-input-placeholder {
  font-family: "Ghotem", "Roboto", sans-serif;
  font-size: 1rem;
  line-height: 0.9375rem;
  color: #cecece;
  padding-left: 2.5rem;
}

header .header-container .flex-container .head-search .srch-inp:-ms-input-placeholder {
  font-family: "Ghotem", "Roboto", sans-serif;
  font-size: 1rem;
  line-height: 0.9375rem;
  color: #cecece;
  padding-left: 2.5rem;
}

header .header-container .flex-container .head-search .srch-inp::-ms-input-placeholder {
  font-family: "Ghotem", "Roboto", sans-serif;
  font-size: 1rem;
  line-height: 0.9375rem;
  color: #cecece;
  padding-left: 2.5rem;
}

header .header-container .flex-container .head-search .srch-inp::placeholder {
  font-family: "Ghotem", "Roboto", sans-serif;
  font-size: 1rem;
  line-height: 0.9375rem;
  color: #cecece;
  padding-left: 2.5rem;
}

header .header-container .flex-container .head-search .send-btn {
  position: absolute;
  top: 50%;
  left: 30px;
  height: 24px;
  width: 24px;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  background-image: url(../../public/img/search.svg);
  background-size: cover;
  background-position: center center;
}

header .header-container .flex-container .account-data {
  padding: 1.0625rem 1.875rem;
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}

header .header-container .flex-container .account-data img {
  margin-right: 0.9375rem;
  height: 42px;
  width: 42px;
  border-radius: 50%;
}

header .header-container .flex-container .account-data .user-text-wraper .user-name span {
  margin-left: 10px;
  font-family: "Ghotem", "Roboto", sans-serif;
  font-size: 0.75rem;
  line-height: 0.6875rem;
  text-transform: uppercase;
  color: rgba(24, 24, 24, 0.2);
}

header .header-container .flex-container .account-data .user-text-wraper .view-text {
  font-family: "Ghotem", "Roboto", sans-serif;
  font-size: 0.75rem;
  line-height: 0.6875rem;
  color: #747474;
  margin-top: 11px;
}

header .header-container .flex-container .account-data .user-text-wraper .view-text .statistic-desc {
  position: relative;
  font-weight: 600;
  margin-left: 5px;
  color: #02b033;
}

header .header-container .flex-container .account-data .user-text-wraper .view-text .statistic-desc:after {
  position: absolute;
  content: "";
  top: 50%;
  right: -75%;
  height: 12px;
  width: 12px;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  background-image: url(../../public/img/arrow-up-right.svg);
  background-position: center center;
  background-size: cover;
}

header .header-container .flex-container .other-wraper {
  padding: 1.875rem 1.4375rem;
  border-left: 1px solid #f4f4f4;
  border-right: 1px solid #f4f4f4;
}

header .header-container .flex-container .other-wraper .other-btn {
  position: relative;
  font-family: "Ghotem", "Roboto", sans-serif;
  font-size: 0.75rem;
  font-weight: bold;
  line-height: 0.6875rem;
  text-align: center;
  text-transform: uppercase;
  color: #181818;
  border: none;
  background-color: transparent;
}

header .header-container .flex-container .other-wraper .other-btn:after {
  position: absolute;
  content: "";
  top: -200%;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  height: 24px;
  width: 24px;
  background-image: url(../../public/img/more-horizontal.svg);
  background-size: contain;
  background-position: center center;
}

@media screen and (max-width: 900px) {
  body {
    position: relative;
  }
  header .header-container {
    padding: 0;
  }
  header .header-container .flex-container {
    -webkit-box-pack: end;
        -ms-flex-pack: end;
            justify-content: flex-end;
  }
  header .header-container .flex-container .logo {
    display: none;
  }
  header .header-container .flex-container nav {
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    background-color: #fff;
  }
  header .header-container .flex-container .head-search {
    width: 70%;
  }
  header .header-container .flex-container .head-search .send-btn {
    left: 7px;
  }
  header .header-container .flex-container .account-data {
    padding: 0;
    position: absolute;
    top: 10px;
    left: 10px;
  }
  header .header-container .flex-container .account-data .user-text-wraper {
    display: none;
  }
}
