footer {
  background-color: #f7f9fb;
  border-top: 1px solid #e7e7e7;
  padding: 35px 0 55px;
}

footer .container .flex-container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}

footer .container .flex-container .footer-title {
  margin-bottom: 30px;
}

footer .container .flex-container .footer-item-wraper:nth-child(3), footer .container .flex-container .footer-item-wraper:nth-child(4) {
  margin-top: 30px;
}

footer .container .flex-container .footer-item-wraper .footer-list .footer-item {
  margin-bottom: 15px;
}

footer .container .flex-container .footer-item-wraper .footer-list .footer-item a {
  font-family: "Ghotem", "Roboto", sans-serif;
  font-size: 0.75rem;
  font-weight: 400;
  color: #181818;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

footer .container .flex-container .footer-item-wraper .footer-list .footer-item a:hover {
  color: #0275b1;
}

footer .container .flex-container .footer-item-wraper .btn {
  display: block;
  font-family: "Ghotem", "Roboto", sans-serif;
  font-size: 0.75rem;
  text-transform: uppercase;
  width: 180px;
  border-radius: 4px;
  border: 1px solid #0275b1;
  padding: 11px;
  text-align: center;
  margin-bottom: 10px;
  color: #0275b1;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

footer .container .flex-container .footer-item-wraper .btn svg {
  vertical-align: middle;
  margin-left: 30%;
  stroke: #0275b1;
}

footer .container .flex-container .footer-item-wraper .btn:hover {
  background-color: #0275b1;
  color: #fff;
}

footer .container .flex-container .footer-item-wraper .btn:hover svg {
  stroke: #fff;
}

footer .container .flex-container .footer-item-wraper .active {
  background-color: #0275b1;
  color: #fff;
}

footer .container .flex-container .footer-item-wraper .active svg path {
  stroke: #fff;
}

footer .container .flex-container .footer-item-wraper .active:hover {
  color: #0275b1;
  background-color: transparent;
}

footer .container .flex-container .footer-item-wraper .active:hover svg path {
  stroke: #0275b1;
}

footer .container .flex-container .footer-item-wraper .select-lang {
  width: 16.875rem;
}

footer .container .flex-container .footer-item-wraper .select-lang li {
  width: 100%;
  font-family: "Ghotem", "Roboto", sans-serif;
  font-size: 0.75rem;
  font-weight: 600;
  text-transform: uppercase;
  text-align: center;
  color: #181818;
  background-color: #fff;
  padding: 10px;
  cursor: pointer;
}

@media screen and (max-width: 800px) {
  footer {
    padding-bottom: 80px;
  }
  footer .container .flex-container {
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
  }
  footer .container .flex-container .footer-item-wraper {
    width: 50%;
  }
}

@media screen and (max-width: 490px) {
  footer .container .flex-container {
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
  }
  footer .container .flex-container .footer-title {
    text-align: center;
  }
  footer .container .flex-container .footer-item-wraper {
    width: 100%;
  }
  footer .container .flex-container .footer-item-wraper .footer-list li {
    text-align: center;
  }
  footer .container .flex-container .footer-item-wraper .btn {
    width: 90%;
    margin: 20px auto;
  }
  footer .container .flex-container .footer-item-wraper .select-lang {
    width: 100%;
  }
  footer .container .flex-container .footer-item-wraper .select-lang li {
    width: 90%;
    margin: 0 auto;
  }
}
