.net-content-wraper .net-wraper {
  padding: 20px 15px;
  background-color: #fff;
  border-radius: 5px;
  margin-bottom: 20px;
}

.net-content-wraper .net-wraper .net-title {
  font-family: "Ghotem", "Roboto", sans-serif;
  font-size: 1rem;
  font-weight: bold;
  text-transform: uppercase;
  color: #181818;
  margin-bottom: 20px;
}

.net-content-wraper .net-wraper .net-desc {
  font-family: "Ghotem", "Roboto", sans-serif;
  font-size: 0.875rem;
  color: #747474;
}

.net-content-wraper .net-wraper .net-item-container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.net-content-wraper .net-wraper .net-item-container .net-item {
  border-radius: 5px;
  background-color: #e9f0f8;
  max-width: 15.625rem;
  margin: 10px 5px 20px 5px;
  overflow: hidden;
}

.net-content-wraper .net-wraper .net-item-container .net-item:hover .net-user-bg::after {
  opacity: 0.1;
}

.net-content-wraper .net-wraper .net-item-container .net-item:hover .user-avatar {
  -webkit-transform: scale(1.05) translate(-50%, -50%);
          transform: scale(1.05) translate(-50%, -50%);
}

.net-content-wraper .net-wraper .net-item-container .net-item .net-user-bg {
  position: relative;
  overflow: hidden;
}

.net-content-wraper .net-wraper .net-item-container .net-item .net-user-bg::after {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: #fff;
  opacity: 0.5;
  z-index: 0;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.net-content-wraper .net-wraper .net-item-container .net-item .user-avatar {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  height: 100px;
  width: 100px;
  border-radius: 50%;
  z-index: 2;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.net-content-wraper .net-wraper .net-item-container .net-item .user-bg {
  height: 150px;
  width: 100%;
  -o-object-fit: cover;
     object-fit: cover;
}

.net-content-wraper .net-wraper .net-item-container .net-item .net-text-wraper {
  padding: 15px;
}

.net-content-wraper .net-wraper .net-item-container .net-item .net-text-wraper .user-name {
  font-family: "Ghotem", "Roboto", sans-serif;
  font-size: 1rem;
  font-weight: 600;
  color: #181818;
  margin-bottom: 15px;
}
